import "./App.css";
import React, { useCallback, useEffect, useState } from "react";
import { client } from "./client";

function App() {
  const [state, setstate] = useState([]);
  const [loading, setLoading] = useState(false);

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const cleanData = useCallback((rowData) => {
    const cleatItem = rowData.map((item) => {
      const { sys, fields } = item;
      const { id } = sys;
      const title = fields.title;
      const description = fields.description;
      const image = fields.image.fields;
      const allData = { id, title, description, image };
      return allData;
    });
    setstate(cleatItem);
  }, []);
  const getDataFromApi = useCallback(async () => {
    setLoading(true);
    try {
      const res = await client.getEntries({ content_type: "car" });
      const data = await res.items;
      if (data) {
        cleanData(data);
      } else {
        setstate([]);
      }
      setLoading(false);
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  }, [cleanData]);

  useEffect(() => {
    getDataFromApi();
  }, [getDataFromApi]);

  return (
    <div>
      <h1>
        {loading ? (
          <div className="parent-loader">
            <div className="lds-spinner">
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
            </div>
          </div>
        ) : (
          "OK"
        )}
      </h1>
      <p>{state.length > 0 ? [state[0].id] : "no"}</p>
    </div>
  );
}

export default App;
